import React, { useEffect, useState } from "react";
import {
  getMyOrders,
  confirmOrder,
  requestCancelOrder,
  requestReturnOrder,
  payOrder,
} from "../api/order";

//  Import context  ng b d liu
import { useOrderContext } from "../contexts/OrderContext";

function MyOrdersPage() {
  const [orders, setOrders] = useState([]);
  const [loading, setLoading] = useState(true);
  const { refreshFlag, triggerRefresh } = useOrderContext(); //  Hook t context

  useEffect(() => {
    fetchOrders();
  }, [refreshFlag]); //  T ng cp nht khi trang khc trigger

  const fetchOrders = async () => {
    try {
      const res = await getMyOrders();
      setOrders(res.data || []);
    } catch (err) {
      console.error("Li khi ly danh sch n hng:", err);
    } finally {
      setLoading(false);
    }
  };

  const handleAction = async (orderId, actionFn) => {
    try {
      const confirmed = window.confirm("Bn c chc chn thc hin thao tc ny?");
      if (!confirmed) return;

      await actionFn(orderId);
      alert("Cp nht trng thi thnh cng!");
      triggerRefresh(); //  Thng bo cc trang khc cp nht li
      fetchOrders();    //  Cp nht li chnh trang ny
    } catch (err) {
      console.error("Li cp nht trng thi:", err);
      alert("Thao tc tht bi.");
    }
  };

  if (loading) return <div className="text-center mt-4">ang ti d liu...</div>;

  return (
    <div className="container mt-4">
      <h3> n hng ca bn</h3>
      {orders.length === 0 ? (
        <p>Bn cha c n hng no.</p>
      ) : (
        <div className="table-responsive">
          <table className="table table-bordered mt-3">
            <thead className="table-dark">
              <tr>
                <th>ID</th>
                <th>Ngy t</th>
                <th>Trng thi</th>
                <th>Tng tin</th>
                <th>Sn phm</th>
                <th>Hnh ng</th>
              </tr>
            </thead>
            <tbody>
              {orders.map((order) => {
                const status = order.status.toLowerCase();

                return (
                  <tr key={order.id}>
                    <td>{order.id}</td>
                    <td>{new Date(order.created_at).toLocaleString()}</td>
                    <td>
                      <span className="badge bg-info text-dark">{status}</span>
                    </td>
                    <td className="text-danger">
                      {Number(order.total_amount || 0).toLocaleString()} VN
                    </td>
                    <td>
                      <ul className="list-unstyled mb-0">
                        {(order.items || []).map((item) => (
                          <li key={item.id}>
                            SP #{item.product_id} x {item.quantity}
                          </li>
                        ))}
                      </ul>
                    </td>
                    <td>
                      {status === "pending" && (
                        <>
                          <button
                            className="btn btn-warning btn-sm me-1 mb-1"
                            onClick={() => handleAction(order.id, requestCancelOrder)}
                          >
                            Hu n
                          </button>
                          <button
                            className="btn btn-primary btn-sm mb-1"
                            onClick={() => handleAction(order.id, payOrder)}
                          >
                            Thanh ton
                          </button>
                        </>
                      )}

                      {status === "shipped" && (
                        <button
                          className="btn btn-success btn-sm mb-1"
                          onClick={() => handleAction(order.id, confirmOrder)}
                        >
                           nhn
                        </button>
                      )}

                      {status === "completed" && (
                        <button
                          className="btn btn-info btn-sm mb-1"
                          onClick={() => handleAction(order.id, requestReturnOrder)}
                        >
                          Hon tr
                        </button>
                      )}
                    </td>
                  </tr>
                );
              })}
            </tbody>
          </table>
        </div>
      )}
    </div>
  );
}

export default MyOrdersPage;
