import React, { useEffect, useState } from "react";
import { useParams, useNavigate } from "react-router-dom";
import axios from "axios";
import { addToCart } from "../api/cart";

function ProductDetailPage() {
  const { id } = useParams();
  const navigate = useNavigate();
  const [product, setProduct] = useState(null);
  const [quantity, setQuantity] = useState(1);
  const [message, setMessage] = useState("");

  useEffect(() => {
    axios.get(`http://localhost:8000/products/${id}`)
      .then(res => setProduct(res.data))
      .catch(err => console.error("Li ly sn phm:", err));
  }, [id]);

  const handleAddToCart = async () => {
    try {
      await addToCart(product.id, quantity);
      setMessage("  thm vo gi hng!");
    } catch (err) {
      console.error("Li thm gi hng:", err);
      setMessage(" Vui lng ng nhp  thm vo gi hng.");
    }
  };

  const handleBuyNow = () => {
    navigate(`/checkout?buyNow=1&productId=${product.id}&quantity=${quantity}`);
  };

  if (!product) return <div className="text-center mt-5">ang ti sn phm...</div>;

  return (
    <div className="container mt-4">
      <div className="row">
        <div className="col-md-5">
          <img src={`http://localhost:8000${product.img}`} alt={product.name} className="img-fluid rounded" />
        </div>
        <div className="col-md-7">
          <h2>{product.name}</h2>
          <h4 className="text-danger">{product.price.toLocaleString()} VN</h4>
          <p className="text-muted">Cn li: {product.quantity} sn phm</p>

          <div className="mb-3">
            <label htmlFor="quantityInput">S lng:</label>
            <input
              id="quantityInput"
              type="number"
              min="1"
              max={product.quantity}
              value={quantity}
              onChange={e => setQuantity(Number(e.target.value))}
              className="form-control"
              style={{ width: "120px" }}
            />
          </div>

          <div className="d-flex gap-2">
            <button onClick={handleAddToCart} className="btn btn-outline-primary">
               Thm vo gi hng
            </button>
            <button onClick={handleBuyNow} className="btn btn-danger">
              Mua ngay
            </button>
          </div>

          {message && (
            <p className={`mt-3 ${message.startsWith("") ? "text-success" : "text-danger"}`}>
              {message}
            </p>
          )}
        </div>
      </div>
    </div>
  );
}

export default ProductDetailPage;