import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { loginAdmin } from '../../api/auth';
import { jwtDecode } from 'jwt-decode'; //  ng c php

const AdminLoginPage = () => {
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const [error, setError] = useState('');
  const [loading, setLoading] = useState(false);
  const navigate = useNavigate();

  const handleLogin = async (e) => {
    e.preventDefault();
    setError('');
    setLoading(true);

    try {
      const res = await loginAdmin(username, password);

      if (res.access_token) {
        const decoded = jwtDecode(res.access_token); //  Dng ng tn hm

        if (decoded.role === 'admin') {
          // Lu token v chuyn hng
          localStorage.setItem('access_token', res.access_token);
          localStorage.setItem('admin_logged_in', 'true');
          navigate('/admin/dashboard');
        } else {
          setError('Bn khng c quyn truy cp.');
        }
      } else {
        setError('ng nhp khng hp l.');
      }
    } catch (err) {
      console.error("Li ng nhp:", err);
      setError('Tn ng nhp hoc mt khu khng ng.');
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="container d-flex justify-content-center align-items-center min-vh-100 bg-light">
      <div className="card shadow" style={{ width: '400px' }}>
        <div className="card-body">
          <h2 className="text-center mb-4">ng nhp qun tr</h2>
          <form onSubmit={handleLogin}>
            <div className="mb-3">
              <label htmlFor="username" className="form-label">Tn ng nhp</label>
              <input
                type="text"
                className="form-control"
                id="username"
                value={username}
                onChange={(e) => setUsername(e.target.value)}
                placeholder="Nhp tn ng nhp"
                required
              />
            </div>

            <div className="mb-3">
              <label htmlFor="password" className="form-label">Mt khu</label>
              <input
                type="password"
                className="form-control"
                id="password"
                value={password}
                onChange={(e) => setPassword(e.target.value)}
                placeholder="Nhp mt khu"
                required
              />
            </div>

            {error && <div className="alert alert-danger">{error}</div>}

            <button type="submit" className="btn btn-primary w-100" disabled={loading}>
              {loading ? 'ang ng nhp...' : 'ng nhp'}
            </button>
          </form>
        </div>
      </div>
    </div>
  );
};

export default AdminLoginPage;
